.form-section {
  text-align: center;
  width: 100vw;
  background: #064271;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: block;
}

input {
  height: 25px;
  width: 200px;
  border: 3px double #838181;
  border-radius: 16px;
  padding: 0 15px;
}

button {
  height: 31px;
  width: 75px;
  border-radius: 16px;
}

.hidden {
  display: none;
}

.empty-gallery {
  text-align: center;
  justify-content: center;
  display: block;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  display: grid;
}

.photo-card img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 20px;
}

.info {
  text-align: center;
  grid-template-columns: repeat(2, 2fr);
  justify-content: center;
  display: grid;
}

